.page-heading {
  margin-bottom: $spacing-6;

  @include media(">=uol-media-m") {
    margin-bottom: $spacing-7;
  }

  @include media(">=uol-media-l") {
    margin-bottom: $spacing-8;
  }

  .uol-article--profile & {
    margin-bottom: $spacing-4;
  }
}

  .page-heading__title {
    @extend .uol-typography-heading-1;

    position: relative;
    margin-bottom: $spacing-2;

    @include media(">=uol-media-m") {
      margin-bottom: $spacing-4;
    }

    &::before {
      content: "";
      position: absolute;
      display: inline-block;
      top: -#{$spacing-2};
      left: 0;
      width: $spacing-4;
      height: $spacing-2;
      background-color: $color-brand;

      @include media(">=uol-media-m") {
        width: $spacing-5;
      }

      @include media(">=uol-media-l") {
        width: $spacing-6;
        top: -#{$spacing-4};
      }
    }

    &.page-heading__title--subtitle {
      @extend %text-size-heading-2;
      @extend %uol-font-serif;

      display: block;
      padding-top: $spacing-2;
      font-weight: $font-weight-bold--serif;

      &::before {
        content: none;
      }
    }
  }

  .page-heading__meta {
    @extend .uol-typography-paragraph-small;

    margin-bottom: 0;
  }

    .page-heading__meta__group {
      margin-bottom: $spacing-1;
    }

    .page-heading__meta__term {
      @extend .hide-accessible;
    }

    .page-heading__meta__data {
      display: inline-block;
      margin: 0;
      color: $color-font--x-light;
      font-variant-numeric: lining-nums;

      .uol-article--profile &,
      .uol-article--person-insight & {
        @extend %text-size-heading-6;
        @extend %uol-font-serif;

        font-weight: $font-weight-bold--serif;
        color: $color-brand--bright;
      }
    }

      .page-heading__meta__data__link {
        color: $color-brand;
        text-decoration: none;
        text-decoration-color: rgba($color-white, 0);
        transition: text-decoration-color 0.3s ease;

        &:hover,
        &:focus {
          text-decoration: underline;
          text-decoration-color: inherit;
        }

        &::after {
          content: ",";
          position: relative;
          left: -0.3em;
        }

        .page-heading__meta__data:last-of-type & {
          &::after {
            content: none;
          }
        }
      }
