.uol-in-text-ctas-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.uol-in-text-cta {
  position: relative;
  box-sizing: border-box;
  margin-bottom: $spacing-4;
  padding: $spacing-5;
  width: 100%;
  background-color: $color-grey--faded;
  border: 2px solid $color-border--light;
  border-radius: 10px;

  @include media(">=uol-media-l") {
    width: calc(50% - #{$spacing-3});
    margin-bottom: $spacing-5;
  }

  @include media(">=uol-media-xl") {
    width: calc(50% - #{$spacing-4});
    margin-bottom: $spacing-6;
  }

  .uol-in-text-cta__heading {
    @extend %uol-font-sans-serif;
    @extend %text-size-heading-6;

    margin: 0 0 0.25rem;
    color: $color-font;
    line-height: 1.4;
    font-weight: 600;

    @include media(">=uol-media-xl") {
      line-height: 1.4;
    }
  }

  .uol-in-text-cta__link {
    text-decoration: none;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }

    &:hover {
      text-decoration: underline;

      svg {
        left: 0.4em;
      }
    }

    &:hover,
    &:focus-within {
      text-decoration: underline;

      &::before {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15), 0 3px 6px rgba(0, 0, 0, 0.15);
      }
    }

    svg {
      display: inline-block;
      fill: $color-brand;
      position: relative;
      top: 0.3rem;
      left: 0;
      margin-left: 0.5em;
      transition: left 0.3s ease 0.2s;

      @media (forced-colors: active) {
        fill: WindowText;
      }

      @media (-ms-high-contrast: active) {
        fill: CanvasText;
      }

      @include media(">=uol-media-m") {
        top: 0.4rem;
      }
    }
  }

  .uol-in-text-cta__text {
    @extend %text-size-paragraph--small;

    line-height: 1.625;
    margin: 0;
    color: $color-font--light;

    @include media(">=uol-media-xl") {
      line-height: 1.556;
    }
  }
}
