.uol-homepage-content-container {
  @extend .uol-col-container;

  overflow: hidden;
  padding: $spacing-6 0 0;
  display: flex;

  @include media("<uol-media-l") {
    display: block;
  }

  .uol-banner-outer + .uol-content-container & {
    padding: 0;
  }
}

.uol-homepage-content {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  padding: 0 $spacing-2;
  width: 100%;
  margin-top: 0;

  .uol-side-nav-container--populated + & {

    @include media(">=uol-media-l") {
      width: calc(75% - 2.65rem);
      margin-left: 0.4rem;
      margin-right: auto;
    }

    @include media(">=uol-media-xl") {
      width: calc(75% - 3rem);
      margin-left: auto;
      margin-right: auto;
    }
  }
}
