.uol-rich-text {
  @extend %uol-font-sans-serif;

  max-width: 47.5rem;

  color: $color-font;
  // Remove margin-top from fist children

  :first-child {
    margin-top: 0;
  }

  > *,
  > * > * {
    max-width: 47.5rem;
  }

  img {
    width: 100%;
    max-width: 47.5rem;
    height: auto;
    margin-top: $spacing-6;
    margin-bottom: $spacing-6;

    @include media(">=uol-media-l") {
      margin-top: $spacing-7;
      margin-bottom: $spacing-7;
    }
  }

  p + img {
    margin-top: $spacing-4;

    @include media(">=uol-media-l") {
      margin-top: $spacing-5;
    }
  }

  .fluid-width-video-wrapper {
    width: 100%;
    max-width: none;
    margin-top: $spacing-6;
    margin-bottom: $spacing-6;

    @include media(">=uol-media-l") {
      margin-top: $spacing-7;
      margin-bottom: $spacing-7;
    }

    iframe {
      width: 100%;
      max-width: none;
    }
  }

  figure {
    width: 100%;
    margin-top: $spacing-6;
    margin-bottom: $spacing-6;

    @include media(">=uol-media-l") {
      margin-top: $spacing-7;
      margin-bottom: $spacing-7;
    }

    img {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  strong {
    @extend %uol-font-sans-serif--bold;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @extend %uol-font-serif;

    font-weight: $font-weight-bold--serif;
    margin: 1.2em 0 0.4em;
    font-variant-numeric: lining-nums;

    strong {
      @extend %uol-font-serif--bold;
    }
  }

  h1 {
    @extend %text-size-heading-1;
  }

  h1.epic {
    @extend %text-size-epic;
  }

  h2 {
    @extend %text-size-heading-2;
  }

  h3 {
    @extend %text-size-heading-3;
  }

  h4 {
    @extend %text-size-heading-4;
  }

  h5 {
    @extend %text-size-heading-5;
  }

  h6 {
    @extend %text-size-heading-6;
  }

  p {
    @extend %text-size-paragraph;

    margin-top: $spacing-2;
    margin-bottom: $spacing-5;

    @include media(">=uol-media-l") {
      margin-bottom: $spacing-6;
    }
  }

  img + p {
    margin-top: 0;
  }

  blockquote {
    @extend %text-size-blockquote;
    @extend %uol-font-serif;

    font-weight: $font-weight-bold--serif;
    position: relative;
    margin: 3em 0 $spacing-7;
    padding: 0 1.75em;

    @include media(">=uol-media-m") {
      padding: 0 1.7em;
      margin-bottom: $spacing-8;
    }

    @include media(">=uol-media-l") {
      padding: 0 1.5em;
      margin-bottom: $spacing-9;
    }

    &::before {
      content: "\201C";
      @extend %uol-font-serif--bold;

      font-size: 400%;
      height: 1ex;
      display: block;
      position: absolute;
      top: -0.125em;
      left: 0;
      color: $color-brand;
      line-height: 0.3;

      @media (forced-colors: active) {
        font-size: 300%;
      }
    }

    p {
      @extend %text-size-blockquote;

      margin-bottom: 0.5em;
    }

    footer {
      @extend %text-size-caption;

      cite {
        @extend %uol-font-sans-serif--italic;

        color: $color-font--x-light;

        a {
          @include link_focus();

          color: inherit;

          &:hover {
            text-decoration-color: $color-brand;
          }
        }
      }
    }
  }

  hr {
    @extend .uol-typography-hr;
  }

  ul,
  ol {
    @extend %text-size-paragraph;

    padding-left: 2em;
    margin-bottom: $spacing-6;

    @include media(">=uol-media-m") {
      margin-bottom: $spacing-7;
    }

    @include media(">=uol-media-l") {
      margin-bottom: $spacing-8;
    }

    li {
      margin-bottom: 0.6em;
    }

    ul,
    ol {
      margin-top: 0.5em;
      margin-bottom: 0;

      :first-child {
        margin-top: 0.5em;
      }
    }
  }

  ol {
    list-style: none;
    counter-reset: item;

    > li {
      position: relative;
      counter-increment: item;

      &::before {
        @extend %uol-font-sans-serif--bold;

        content: counter(item) ".";
        font-variant-numeric: lining-nums;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        width: 2em;
        transform: translateX(-126%);
        text-align: right;
      }
    }
  }

  p,
  li {
    a {
      @include link_focus();
    }
  }

  // WordPress/TinyMCE specifics
  .wp-caption-text,
  figcaption {
    @extend %text-size-caption;
    @extend %uol-font-sans-serif;

    color: $color-font--x-light;

    a {
      @include link_focus();

      font-style: italic;
      color: inherit;
    }
  }

  // JADU/CKEditor
  blockquote {

    footer {

      p,
      cite {
        @extend %text-size-caption;
        @extend %uol-font-sans-serif--italic;

        color: $color-font--x-light;
      }
    }
  }
}

  .uol-rich-text--with-lead {
    > p:first-of-type {
      @extend .uol-typography-paragraph-intro;

      font-weight: $font-weight-bold--sans-serif;
    }
  }
