.uol-article-container {

}

.uol-article {
  @extend .uol-col;
  @extend .uol-col-m-10;
  @extend .uol-col-l-8;
  @extend .uol-col-xl-6;

  position: relative;
  margin: 0 auto;

  @include media("<uol-media-m") {
    margin-left: $spacing-2;
    margin-right: $spacing-2;
  }

  .uol-rich-text {
    max-width: none;
  }

    .uol-rich-text--with-lead {

      > p:first-of-type {

        @include media(">=uol-media-m") {
          position: relative;

          &::before {
            content: "";
            display: inline-block;
            position: absolute;
            width: $spacing-7;
            height: $spacing-1;
            top: 0.75em;
            left: -#{$spacing-4};
            transform: translateX(-100%);
            background-color: $color-brand;
          }
        }

        @include media(">=uol-media-l") {
          &::before {
            width: $spacing-8;
            left: -#{$spacing-5};
          }
        }

        @include media(">=uol-media-xl") {
          &::before {
            left: -#{$spacing-6};
          }
        }
      }
    }

    .uol-side-nav-container--populated + & {
      .uol-rich-text--with-lead {
        > p:first-of-type {
          @include media(">=uol-media-m") {
            &::before {
              content: none;
            }
          }

          @include media(">=uol-media-xl") {
            &::before {
              content: "";
            }
          }
        }
      }
    }

  .uol-typography-pull-quote {
    box-sizing: border-box;
    width: 100%;

    .uol-col-debug & {
      background: rgba(hotpink, 0.5);
    }

    @include media(">=uol-media-m") {
      float: right;
      max-width: calc(50% - #{$spacing-2});
      margin-right: -10%;
      margin-bottom: $spacing-4;
      margin-left: $spacing-4;
    }

    @include media(">=uol-media-l") {
      margin-right: -26%;
      margin-bottom: $spacing-5;
      margin-left: $spacing-5;
      max-width: calc(50% - #{$spacing-3});
    }

    @include media(">=uol-media-xl") {
      float: none;
      position: absolute;
      margin-right: auto;
      margin-bottom: 0;
      margin-left: $spacing-4;
      left: 100%;
      max-width: calc(50% - 2rem);
    }
  }

  .uol-side-nav-container--populated + & {
    .uol-typography-pull-quote {
      @include media(">=uol-media-l") {
        margin-right: 0;
      }

      @include media(">=uol-media-xl") {
        position: initial;
        float: right;
        margin-right: -25%;
      }
    }
  }

    .uol-featured-image,
    figure {
      @extend .uol-featured-image;

      @include media("<=uol-media-xs") {
        margin-right: -#{$spacing-4};
        margin-left: -#{$spacing-4};
        max-width: none;
      }

      @include media(">uol-media-xs", "<uol-media-s") {
        margin-right: -#{$spacing-5};
        margin-left: -#{$spacing-5};
        max-width: none;
      }

      img {
        @extend .uol-featured-image__img;
      }
    }

    .uol-featured-image__caption,
    figcaption {
      @extend .uol-featured-image__caption;

      @include media("<uol-media-s") {
        margin-right: $spacing-4;
        margin-left: $spacing-4;
      }
    }
}

  .uol-article--event {

    @include media(">=uol-media-xl") {
      transform: translateX(-16.65%);
    }

    .uol-article__content {
      position: relative;
    }

    .uol-info-list-container {

      .uol-col-debug & {
        background: rgba(hotpink, 0.5);
      }

      @include media(">=uol-media-xl") {
        margin: 0;
        padding: 0 $spacing-4;
        width: calc(50% - 1rem);
        position: absolute;
        right: -#{$spacing-4};
        top: 0;
        transform: translateX(100%);
      }
    }

    .uol-info-list {
      @include media(">=uol-media-xl") {
        margin: 0;
      }
    }
  }

  .uol-article--profile {

    $profileWidth: (10 / $col-count) * 100 + "%";

    @include media(">=uol-media-s") {
      flex-basis: calc(#{$profileWidth} - #{$col-gutter-s});
      max-width: calc(#{$profileWidth} - #{$col-gutter-s});
    }

    $profileWidth: (11 / $col-count) * 100 + "%";

    @include media(">=uol-media-l") {
      flex-basis: calc(#{$profileWidth} - #{$col-gutter-l});
      max-width: calc(#{$profileWidth} - #{$col-gutter-l});
      margin: 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
    }

    $profileWidth: (10 / $col-count) * 100 + "%";

    @include media(">=uol-media-xl") {
      flex-basis: calc(#{$profileWidth} - #{$col-gutter-xl});
      max-width: calc(#{$profileWidth} - #{$col-gutter-xl});
      margin: 0;
    }

    .uol-article__head {
      display: flex;
      flex-direction: column-reverse;

      @include media(">=uol-media-xs") {
        flex-wrap: wrap;
        justify-content: flex-end;
        flex-direction: row-reverse;
      }

      @include media(">=uol-media-l") {
        width: 100%;
        flex-direction: row;
      }
    }

    .page-heading {
      flex-basis: 70.8%;

      @include media(">=uol-media-xs") {
        flex-basis: 73.8%;
      }
      @include media(">=uol-media-s") {
        flex-basis: unset;
      }
      @include media(">=uol-media-l") {
        flex-basis: calc(74.6% - 1.5rem);
      }
      @include media(">=uol-media-xl") {
        flex-basis: calc(60.9% - 1.5rem);
      }
    }

    .uol-info-list-container,
    .uol-article-section__content {

      @include media(">=uol-media-l") {
        flex-basis: calc(74.6% - #{$col-gutter-l});
        max-width: calc(74.8% - #{$col-gutter-l});
        margin-left: 0;
        float: right;
      }

      @include media(">=uol-media-xl") {
        flex-basis: calc(61.6% - #{$col-gutter-xl});
        max-width: calc(61.6% - #{$col-gutter-xl});
        margin-left: 0;
      }
    }

    .uol-profile-image {
      margin-top: $spacing-6;
      margin-right: $spacing-4;
      flex-basis: 5rem;
      max-width: 5rem;

      @include media(">=uol-media-xs") {
        // flex-basis: calc(26.1% - #{$col-gutter-s});
        // max-width: calc(26.1% - #{$col-gutter-s});
      }

      @include media(">=uol-media-s") {
        flex-basis: calc(31.1% - #{$col-gutter-s});
        max-width: calc(31.1% - #{$col-gutter-s});
        margin-left: -#{$spacing-7};
      }

      @include media(">=uol-media-m") {
        flex-basis: calc(31.1% - #{$col-gutter-m});
        max-width: 11rem;
        // margin-left: -#{$spacing-8};
      }

      @include media(">=uol-media-l") {
        position: absolute;
        top: 0;
        right: 72.5%;
        width: 12rem;
        margin-left: 0;
      }

      @include media(">=uol-media-xl") {
        right: 60%;
      }
    }

    .uol-info-list-container {
      @include media(">=uol-media-l") {
        border-bottom: $spacing-1 solid $color-brand--bright;
        margin-bottom: $spacing-7;
      }
    }

    .uol-article__content {

    }

    .uol-article-section {
      margin-bottom: $spacing-5;

      @include media("<uol-media-l") {
        border-top: $spacing-1 solid $color-brand--bright;
      }

      @include media(">=uol-media-l") {
        clear: both;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        margin-bottom: $spacing-7;
      }

      + .uol-article-section {
        border-top: none;
      }
    }

      .uol-article-section__title {
        @include font-size-responsive(1.75rem, 2.25rem, 1.75rem);
        @include line-height-responsive(1.75rem, 2.25rem, 2.25rem);
        @extend %uol-font-serif--bold;

        .uol-col-debug & {
          background: rgba(hotpink, 0.5);
        }

        margin: $spacing-6 0 $spacing-3;

        @include media(">=uol-media-l") {
          flex-basis: calc(28% - #{$col-gutter-l});
          max-width: calc(28% - #{$col-gutter-l});
          margin-left: 0;
          margin-top: 0;
          position: relative;
          left: -#{$spacing-5};
          text-align: right;
        }

        @include media(">=uol-media-xl") {
          flex-basis: calc(40.9% - #{$col-gutter-xl});
          max-width: calc(40.9% - #{$col-gutter-xl});
          left: -#{$spacing-6};
        }
      }

      .uol-article-section__content {
        .uol-col-debug & {
          background: rgba(hotpink, 0.5);
        }
      }
  }

  .uol-article--person-insight {
    // background-color: hotpink;

    .uol-article__head {
      display: flex;
    }

    .page-heading {
      width: calc(100% - #{$spacing-9} - #{$spacing-4});

      @include media(">=uol-media-s") {
        width: calc(100% - 12rem - #{$spacing-4});
      }

      @include media(">=uol-media-m") {
        width: calc(100% - 10.812rem - #{$spacing-4});
      }

      @include media(">=uol-media-l") {
        width: calc(100% - 12rem - #{$spacing-4});
      }
    }

    .uol-profile-image {
      width: $spacing-9;
      top: $spacing-5;
      margin-left: $spacing-4;

      @include media(">=uol-media-s") {
        width: 12rem;
      }

      @include media(">=uol-media-m") {
        width: 10.812rem;
      }

      @include media(">=uol-media-l") {
        width: 12rem;
      }
    }

    .uol-info-list-container {
      border-bottom: $spacing-1 solid $color-brand--bright;
      margin-bottom: $spacing-6;
    }
  }

  .uol-article__content {
    padding-bottom: $spacing-6;
  }
