.uol-page {
  @extend .uol-col;
  @extend .uol-col-m-10;
  @extend .uol-col-l-9;
  @extend .uol-col-xl-8;

  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: $spacing-7;

  @include media(">=uol-media-m") {
    margin-bottom: $spacing-9;
  }

  @include media(">=uol-media-l") {
    margin-bottom: ($spacing-7 * 2);
  }

  .uol-side-nav-container--populated + & {
    @extend .uol-col;
    @extend .uol-col-l-8;
  }
}
