.uol-news-tiles {
  display: flex;
  flex-wrap: wrap;
}

.uol-news-tile {
  position: relative;
  box-sizing: border-box;
  background-color: $color-black--light;
  border: 1px solid $color-white;
  color: $color-white;
  width: 100%;
  min-height: 12rem;
  padding: $spacing-4;

  @include media(">=uol-media-xs") {
    min-height: 13rem;
  }

  @include media(">=uol-media-s") {
    min-height: 18.75rem;
    width: 50%;

    &:first-of-type {
      width: 100%;
    }
  }

  @include media(">=uol-media-m") {
    min-height: 16rem;
    width: calc(100% / 3);
    padding: $spacing-5;

    &:first-of-type {
      width: calc(100% / 3 * 2);
    }
  }

  @include media(">=uol-media-l") {
    min-height: 21.5rem;
    padding: $spacing-6;
  }

  @include media(">=uol-media-xl") {
    min-height: 22.5rem;
    width: 25%;
    padding: $spacing-5 $spacing-6;

    &:first-of-type {
      width: 50%;
    }
  }

  &:nth-of-type(2) {
    background-color: $color-brand;

    .uol-news-tile__img {
      display: none;
    }
  }

  &:nth-of-type(4) {
    background-color: $color-brand--dark;

    .uol-news-tile__img {
      display: none;
    }
  }

  &:nth-of-type(7) {
    background-color: $color-black;

    .uol-news-tile__img {
      display: none;
    }
  }

  &::after {
    border: 4px solid rgba($color-brand--bright, 0);
  }

  &:focus-within {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-color: $color-brand--bright;
    }
  }
}

  .uol-news-tile__inner {
    z-index: 8;
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &::before {
      content: "";
      position: absolute;
      display: inline-block;
      top: 0;
      left: 0;
      width: $spacing-4;
      height: $spacing-1;
      background-color: $color-white;
      transition: background 0.3s ease;
      @include media(">=uol-media-m") {
        width: spacing-5;
      }

      @include media(">=uol-media-l") {
        width: $spacing-6;
        height: $spacing-2;
      }
    }

    .uol-news-tile:hover &,
    .uol-news-tile:focus-within & {
      &::before {
        background-color: $color-brand--bright;
      }
    }

    .uol-news-tile:nth-of-type(2) &,
    .uol-news-tile:nth-of-type(4) &,
    .uol-news-tile:nth-of-type(7) & {
      &::before {
        background-color: $color-white;
      }
    }
  }

  .uol-news-tile__title {
    @extend .uol-typography-heading-4;

    margin: 0;
    padding-top: $spacing-4;
    font-variant-numeric: lining-nums;
  }

  .uol-news-tile__link {
    color: inherit;
    text-decoration: none;
    background: rgba($color-black--dark, 0.6);
    box-shadow: -#{$spacing-2} 0 0 0 rgba($color-black--dark, 0.6), #{$spacing-2} 0 0 0 rgba($color-black--dark, 0.6);

    &::after {
      content: "";
      position: absolute;
      top: -#{$spacing-4};
      right: -#{$spacing-4};
      bottom: -#{$spacing-4};
      left: -#{$spacing-4};

      @include media(">=uol-media-l") {
        top: -#{$spacing-5};
        right: -#{$spacing-5};
        bottom: -#{$spacing-5};
        left: -#{$spacing-5};
      }
    }

    &:hover,
    &:focus {
      text-decoration: underline;
      text-decoration-color: $color-brand--bright;
    }

    .uol-news-tile:nth-of-type(2) &,
    .uol-news-tile:nth-of-type(4) &,
    .uol-news-tile:nth-of-type(7) & {
      background: transparent;
      box-shadow: none;

      &:hover,
      &:focus {
        text-decoration-color: $color-white;
      }
    }
  }

  .uol-news-tile__meta {
    @extend .uol-typography-paragraph-small;

    margin: $spacing-5 0 0;
  }

    .uol-news-tile__meta__term {
      @extend .hide-accessible;
    }

    .uol-news-tile__meta__data {
      display: inline;
      margin: 0;

      + .uol-news-tile__meta__data {
        &::before {
          content: ",";
          position: relative;
          left: -0.2em;
        }
      }
    }

      .uol-news-tile__meta__data--date {
        display: inline-block;
        font-variant-numeric: lining-nums;
        background: rgba($color-black--dark, 0.6);
        box-shadow: -#{$spacing-2} 0 0 0 rgba($color-black--dark, 0.6), #{$spacing-2} 0 0 0 rgba($color-black--dark, 0.6);

        .uol-news-tile:nth-of-type(2) &,
        .uol-news-tile:nth-of-type(4) &,
        .uol-news-tile:nth-of-type(7) & {
          background: transparent;
          box-shadow: none;
        }
      }

    .uol-news-tile__meta__link {
      color: inherit;
      text-decoration: none;

      &:hover,
      &:focus {
        text-decoration: underline;
      }
    }

  .uol-news-tile__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-position: 50%;
    background-size: cover;

    @media (-ms-high-contrast: active), (forced-colors: active) {
      -ms-high-contrast-adjust: none;
      forced-color-adjust: CanvasText;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba($color-black, 0.1);
      transition: background 0.3s ease;

      .uol-news-tile:hover &,
      .uol-news-tile:focus-within & {
        background-color: rgba($color-black, 0);
      }
    }
  }
